.woocommerce-mini-cart__empty-message
    @apply font-barlow text-[20px] leading-[28px] text-accent font-normal

.woocommerce-mini-cart
    &__buttons
        @apply cursor-pointer transition-colors duration-300 ease-in-out
        .button.wc-forward, .button.checkout
            @apply text-dark border-2 border-accent bg-accent font-barlow text-[12px] md:text-[18px] mt-4 block font-bold leading-[8.811px] md:leading-[13px] uppercase text-center py-[6.5px] md:py-[16.5px]
            &:hover
                @apply bg-dark text-accent
            &:active
                @apply bg-accent text-dark
    &-item.mini_cart_item
        @apply mb-2
body.woocommerce-cart, body.woocommerce-checkout
    @apply bg-background
    
    .ab-steps
        .step-dot
            display: inline-flex
            width: 34px
            height: 34px
            border-radius: 9999px
            align-items: center
            justify-content: center
            border: 2px solid #c7a04a
            font-weight: 600
            &-faded
                display: inline-flex
                width: 34px
                height: 34px
                border-radius: 9999px
                align-items: center
                justify-content: center
                border-color: #B1B5C3
                background: #B1B5C3
                color: #fff
                font-weight: 600
            &-done
                display: inline-flex
                width: 34px
                height: 34px
                border-radius: 9999px
                align-items: center
                justify-content: center
                border-color: #38CB89
                background: #38CB89
                color: #fff
                font-weight: 600
        .step-active
            .step-dot
                background: #c7a04a
                color: #fff

    .woocommerce
        table.shop_table
            @apply border-0
            thead
                @apply border-b border-[#6C7275]
            th
                @apply pl-0 pb-6 font-barlow text-[16px] leading-[26px] font-semibold text-dark
            td
                @apply border-0
        &-cart
            table.cart
                td
                    @apply px-0 pt-6 pb-6
                input
                    @apply bg-transparent

    #add_payment_method table.cart,
    .woocommerce-cart table.cart,
    .woocommerce-checkout table.cart
        img
            width:100% !important
            height: 100%

    .woocommerce-cart-form__cart-item
        .product-quantity, .product-price, .product-subtotal
            @apply font-barlow text-[16px] leading-[26px] font-semibold text-dark

    .woocommerce-shipping-methods
        @apply border border-dark
        margin-top: 12px !important
        margin-bottom: 12px !important
        padding-left: 13px !important
        padding-top: 13px !important
        padding-bottom: 13px !important
        li
            margin: 0 !important

    .woocommerce-checkout-fields
        h3
            @apply font-barlow text-[20px] leading-[28px] text-black font-medium mb-6

    #payment 
        #place_order
            @apply text-dark border-2 border-accent bg-accent font-barlow text-[12px] md:text-[18px] font-bold leading-[8.811px] md:leading-[20px] uppercase text-center px-[8.5px] md:px-[62px] py-[6.5px] md:py-[16.5px] float-none mt-4
            &:hover
                @apply bg-dark text-accent
            &:active
                @apply bg-accent text-dark

//order complete
.ab-thankyou
    background: #f2f2f2

.ab-card
    box-shadow: 0 1px 2px rgba(0,0,0,.04), 0 16px 48px rgba(0,0,0,.08)

.ab-thumb
    position: relative
    border-radius: 8px
    overflow: hidden
    background: #f3f4f6

.ab-badge
    display: inline-flex
    align-items: center
    justify-content: center
    width: 26px
    height: 26px
    border-radius: 9999px
    background: #111
    color: #fff
    font-weight: 700
    font-size: 13px
    line-height: 1
    box-shadow: 0 0 0 3px #fff
